<template>
  <div>
    <Card>
      <div slot="title">订单统计</div>
      <div slot="content">
        <!-- 时间搜索 -->
        <el-date-picker
          v-model="timeArr"
          value-format="yyyy-MM-dd"
          size="mini"
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
        ></el-date-picker>
        <el-button size="mini" type="primary" @click="submitBtn">提交</el-button>
        <div class="box">
          <Echarts v-if="isExit" :options="options" />
        </div>
      </div>
    </Card>
  </div>
</template>

<script>
import Card from "@/components/Card";
import Echarts from "@/components/Echarts";
import { api_getOrderData } from "@/api/order";
import moment from "moment"
export default {
  components: {
    Card,
    Echarts,
  },
  data() {
    return {
        isExit:false,
        timeArr:"[]",
      options: {
        title: "订单统计",
        legend: ["订单报表"],
        xData: [], //x轴数据  下单时间
        orderData: [], // 订单金额
        amountData: [], //所有订单数据
      },
    };
  },
  methods: {
    async getOrderData() {
          this.isExit = false

      let {data} = await api_getOrderData({
        date: this.timeArr==null?JSON.stringify([]):JSON.stringify(this.timeArr)
      });
      
       // 赋值
      this.options.xData = data.map(item =>
        moment(item.orderTime).format("YYYY-MM-DD")
      ); // x轴赋值
      this.options.orderData = data.map(item => item.orderAmount); // y轴赋值

      this.isExit = true;
    },
    submitBtn(){
        this.getOrderData()
    }
  },
  created(){
      this.getOrderData()
  }
};
</script>

<style lang="less" scoped>
.box {
  width: 100%;
  height: 500px;
}
</style>